import React, { FC } from "react";
import {
  Paper,
  styled,
  Table,
  TableBody,
  TableCell,
  tableCellClasses,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { Match } from "../../../services/matchesService";
import { MatchPlayer, useMatchPlayer } from "../../../services/playersService";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

type MatchSummaryProps = {
  summary: Match;
};

const MatchSummary: FC<MatchSummaryProps> = ({ summary }) => {
  const {
    homeId,
    homeSets,
    homeSet1,
    homeSet2,
    homeSet3,
    homeSet4,
    homeSet5,
    awayId,
    awaySets,
    awaySet1,
    awaySet2,
    awaySet3,
    awaySet4,
    awaySet5,
  } = summary;

  const {
    data: homePlayer,
    isLoading: homeIsLoading,
    isError: homeIsError,
  } = useMatchPlayer(`${homeId}`);
  const {
    data: awayPlayer,
    isLoading: awayIsLoading,
    isError: awayIsError,
  } = useMatchPlayer(`${awayId}`);

  if (homeIsLoading || awayIsLoading) {
    return <Typography>Loading...</Typography>;
  }

  if (homeIsError || awayIsError) {
    return <Typography>Error Ocurred</Typography>;
  }

  const { name: homeName, country: homeCountry } =
    homePlayer?.player as MatchPlayer;
  const { name: awayName, country: awayCountry } =
    awayPlayer?.player as MatchPlayer;

  return (
    <TableContainer
      sx={{ maxWidth: "700px", margin: "0 auto" }}
      component={Paper}
    >
      <Table aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell colSpan={2}></StyledTableCell>
            <StyledTableCell align="right">S</StyledTableCell>
            <StyledTableCell align="right">1</StyledTableCell>
            <StyledTableCell align="right">2</StyledTableCell>
            <StyledTableCell align="right">3</StyledTableCell>
            <StyledTableCell align="right">4</StyledTableCell>
            <StyledTableCell align="right">5</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <StyledTableRow>
            <StyledTableCell component="th" colSpan={2} scope="row">
              {homeName} ({homeCountry})
            </StyledTableCell>
            <StyledTableCell align="right">{homeSets}</StyledTableCell>
            <StyledTableCell align="right">{homeSet1}</StyledTableCell>
            <StyledTableCell align="right">{homeSet2}</StyledTableCell>
            <StyledTableCell align="right">{homeSet3}</StyledTableCell>
            <StyledTableCell align="right">{homeSet4}</StyledTableCell>
            <StyledTableCell align="right">{homeSet5}</StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell component="th" colSpan={2} scope="row">
              {awayName} ({awayCountry})
            </StyledTableCell>
            <StyledTableCell align="right">{awaySets}</StyledTableCell>
            <StyledTableCell align="right">{awaySet1}</StyledTableCell>
            <StyledTableCell align="right">{awaySet2}</StyledTableCell>
            <StyledTableCell align="right">{awaySet3}</StyledTableCell>
            <StyledTableCell align="right">{awaySet4}</StyledTableCell>
            <StyledTableCell align="right">{awaySet5}</StyledTableCell>
          </StyledTableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default MatchSummary;
