import { FC } from "react";
import { Bracket } from "../../../services/bracketsService";
import BracketsCard from "../BracketsCard/BracketsCard";

export type BracketsContainerProps = {
  brackets: Bracket[];
};

const BracketsContainer: FC<BracketsContainerProps> = ({ brackets }) => {
  return (
    <div style={{ marginTop: "15px" }}>
      {brackets &&
        brackets.map((bracket) => (
          <BracketsCard key={bracket._id} bracket={bracket} />
        ))}
    </div>
  );
};

export default BracketsContainer;
