import axios from "axios";
import { useQuery } from "react-query";

export type Country = {
  name: string;
  countryFlag: string;
};

export type Tournament = {
  id: number;
  name: string;
  city: string;
  code: string;
  startDate: string;
  endDate: string;
  countryName: string;
  countryFlag: string;
  matchesCount: number;
  surface: string;
};

export type Player = {
  id: number;
  name: string;
  country: string;
};

export type Match = {
  id: number;
  homePlayer: Player;
  awayPlayer: Player;
  homeSets: string;
  awaySets: string;
  winnerId: number;
};

export type GetAllTournamentsViewModel = {
  tournaments: Tournament[];
};

export type GetSingleTournamentMatchesViewModel = {
  matches: Match[];
};

export type GetSingleTournament = {
  tournament: Tournament;
};

// export function useTournaments() {
//     return useQuery(["tournaments"], getAllTournaments);
// }

export function useTournamentsByDate(date: string) {
  return useQuery(["tournaments", date], getAllTournamentsByDate);
}

export function useTournamentsByWeek(weekId: string) {
  return useQuery(["tournamentsByWeek", weekId], () =>
    getAllTournamentsByWeek(weekId)
  );
}

export function useSingleTournamentMatches(id?: string, date?: string) {
  return useQuery(
    [`tournaments/${id}/matches`, id, date],
    getSingleTournamentMatches
  );
}

export function useSingleTournament(id?: string) {
  return useQuery([`tournaments/${id}`, id], getSingleTournament);
}

const getAllTournaments = async () => {
  const response = await axios.get<GetAllTournamentsViewModel>(
    "/api/v1/tournaments"
  );
  const result = response.data;
  return result;
};

const getAllTournamentsByDate = async ({
  queryKey,
}: {
  queryKey: string[];
}) => {
  const response = await axios.get<GetAllTournamentsViewModel>(
    `/api/v1/tournaments/byDate/${queryKey[1]}`
  );
  const result = response.data;
  return result;
};

export const getAllTournamentsByWeek = async (weekId: string) => {
  const response = await axios.get<GetAllTournamentsViewModel>(
    `/api/v1/tournaments/byWeek?weekId=${weekId}`
  );
  const result = response.data;

  return result;
};

const getSingleTournamentMatches = async ({
  queryKey,
}: {
  queryKey: (string | undefined)[];
}) => {
  const response = await axios.get<GetSingleTournamentMatchesViewModel>(
    `/api/v1/tournaments/${queryKey[1]}/matches/byDate/${queryKey[2]}`
  );
  const result = response.data;

  return result;
};

const getSingleTournament = async ({
  queryKey,
}: {
  queryKey: (string | undefined)[];
}) => {
  const response = await axios<GetSingleTournament>(
    `/api/v1/tournaments/${queryKey[1]}`
  );
  const result = response.data;

  return result;
};

