import React, { FC } from "react";
import { Match } from "../../../services/matchesService";
import { Box, Grid, Typography } from "@mui/material";
import MatchPlayerCard from "./MatchPlayerCard";

type MatchPlayersCardProps = {
  homeId: number;
  homeSets: string;
  awayId: number;
  awaySets: string;
  status: string;
  date: string;
};

const MatchPlayersCard: FC<MatchPlayersCardProps> = ({
  homeId,
  homeSets,
  awayId,
  awaySets,
  status,
  date,
}) => {
  const parsedDate = new Date(date).toLocaleDateString("en-CA");

  return (
    <Grid
      container
      sx={{ maxWidth: "720px", margin: "0 auto" }}
      padding="16px"
      justifyContent={"center"}
    >
      <Grid item xs={4} sm={4}>
        <MatchPlayerCard id={homeId} />
      </Grid>
      <Grid item xs={4} sm={4}>
        <Typography textAlign="center">{parsedDate}</Typography>
        <Typography textAlign="center" fontWeight="bolder">
          {homeSets} - {awaySets}
        </Typography>
        <Typography
          textAlign="center"
          textTransform="capitalize"
          fontWeight="bolder"
        >
          {status}
        </Typography>
      </Grid>
      <Grid item xs={4} sm={4}>
        <MatchPlayerCard id={awayId} />
      </Grid>
    </Grid>
  );
};

export default MatchPlayersCard;
