import { useQuery, useMutation, useQueryClient } from "react-query";
import axios from "axios";
import { Item } from "../components/shared/ItemsMenu/ItemsMenu";
import { HeadToHeadMatchViewModel, Match } from "./matchesService";

export type Player = {
  _id: string;
  id: number;
  name: string;
  country: string;
  points: number;
  ranking: number;
  price: number;
  imageUrl: string;
  gender: string;
};

export type MatchPlayer = {
  id: number;
  name: string;
  country: string;
  ranking: string;
  gender: string;
  imageUrl: string;
};

export type PlayerInTeam = {
  pointsWon: number;
  balls: number;
} & Player;

export type FilterPlayersInTeamObject = {
  selected: Item;
};

export type FilterPlayersObject = {
  playerSearchItem: string;
  isBought: boolean;
  page: number;
  itemsPerPage: number;
} & FilterPlayersInTeamObject;

export type GetAllPlayersViewModel = {
  players: Player[];
  totalItems: number;
};

export type GetAllPlayersInTeamViewModel = {
  players: PlayerInTeam[];
};

export type GetTeamPointsViewModel = {
  points: number;
};
export type GetSingleMatchPlayerViewModel = {
  player: MatchPlayer;
};

export type GetSinglePlayerMatchesViewModel = {
  player: MatchPlayer,
  matches: HeadToHeadMatchViewModel[],
}

export type AddPlayerToTeamInputModel = {
  weekId: string;
  playerId: number;
};

// React Query Hooks

export function usePlayers() {
  return useQuery(["players"], getAllPlayers);
}

export function useFilteredPlayers(
  filters: FilterPlayersObject,
  enabled: boolean
) {
  return useQuery(
    ["players", filters, enabled],
    () => getAllFilteredPlayers(filters),
    {
      enabled: enabled,
    }
  );
}

export function usePlayersInTeam(filters: FilterPlayersInTeamObject) {
  return useQuery(["team", filters], () => getAllPlayersInTeam(filters));
}

export function useGetWeekly(weekId: string) {
  return useQuery(["team", weekId], () => getWeeklyPoints(weekId));
}

export function useGetTotal() {
  return useQuery(["team"], getTotalPoints);
}

export function useMatchPlayer(id: string) {
  return useQuery([`players/${id}/matches`, id], () => getSingleMatchPlayer(id));
}

export function useSinglePlayerMatches(id: string) {
  return useQuery([`players`, id], () => getSinglePlayerMatches(id));
}

export function useAddPlayerToTeam() {
  const queryClient = useQueryClient();

  return useMutation(addPlayerToTeam, {
    onMutate: () => {
      console.log("useAddPlayerToTeam: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("New player was added!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

export function useDeletePlayerFromTeam() {
  const queryClient = useQueryClient();

  return useMutation(deletePlayerFromTeam, {
    onMutate: () => {
      console.log("useDeletePlayerFromTeam: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("A player was removed!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

export function useAddBallToPlayer() {
  const queryClient = useQueryClient();

  return useMutation(addBallToPlayer, {
    onMutate: () => {
      console.log("useAddBallToPlayer: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("New ball was added to player!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

export function useDeleteBallFromPlayer() {
  const queryClient = useQueryClient();

  return useMutation(deleteBallFromPlayer, {
    onMutate: () => {
      console.log("useDeleteBallFromPlayer: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("A ball was removed from player!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

export function useCalculateWeekly() {
  const queryClient = useQueryClient();

  return useMutation(calculateWeeklyPoints, {
    onMutate: () => {
      console.log("useCalculateWeekly: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("Points were updated!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

export function useCalculateTotal() {
  const queryClient = useQueryClient();

  return useMutation(calculateTotalPoints, {
    onMutate: () => {
      console.log("useCalculateTotal: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("Total points were updated!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["team"]);
    },
  });
}

// API Methods

export const getAllPlayers = async () => {
  const response = await axios.get<GetAllPlayersViewModel>("/api/v1/players");
  const result = response.data.players.slice(0, 5);

  return result;
};

export const getAllFilteredPlayers = async (filters: FilterPlayersObject) => {
  const response = await axios.post<GetAllPlayersViewModel>("/api/v1/players", {
    ...filters,
  });
  const result = response.data;

  return result;
};

export const getAllPlayersInTeam = async (
  filters: FilterPlayersInTeamObject
) => {
  const response = await axios.post<GetAllPlayersInTeamViewModel>(
    "/api/v1/players/team",
    {
      ...filters,
    }
  );
  const result = response.data;

  return result;
};

export const getSingleMatchPlayer = async (id: string) => {
  const response = await axios.get<GetSingleMatchPlayerViewModel>(`/api/v1/players/${id}`); 
  const result = response.data;

  return result;
};

export const getSinglePlayerMatches = async (id: string) => {
  const response = await axios.get<GetSinglePlayerMatchesViewModel>(`/api/v1/players/${id}/matches`);
  const result = response.data;

  return result;
};

export const addPlayerToTeam = async (
  inputModel: AddPlayerToTeamInputModel
) => {
  return await axios.post("/api/v1/players/add", inputModel);
};

export const deletePlayerFromTeam = async (
  inputModel: AddPlayerToTeamInputModel
) => {
  return await axios.delete(
    `/api/v1/players/team?weekId=${inputModel.weekId}&playerId=${inputModel.playerId}`
  );
};

export const addBallToPlayer = async (
  inputModel: AddPlayerToTeamInputModel
) => {
  return await axios.patch("/api/v1/players/addBall", inputModel);
};

export const deleteBallFromPlayer = async (
  inputModel: AddPlayerToTeamInputModel
) => {
  return await axios.patch("/api/v1/players/deleteBall", inputModel);
};

export const calculateWeeklyPoints = async (weekId: string) => {
  return await axios.patch("/api/v1/players/calculateWeekly", { weekId });
};

export const calculateTotalPoints = async () => {
  return await axios.patch("/api/v1/players/calculateTotal");
};

export const getWeeklyPoints = async (weekId: string) => {
  const response = await axios.get<GetTeamPointsViewModel>(
    `/api/v1/players/calculateWeekly?weekId=${weekId}`
  );

  return response.data.points;
};

export const getTotalPoints = async () => {
  const response = await axios.get<GetTeamPointsViewModel>(
    `/api/v1/players/calculateTotal`
  );
  return response.data.points;
};
