import { useNavigate } from "react-router-dom";
import {
  AppBar,
  Box,
  Button,
  IconButton,
  Toolbar,
  Typography,
} from "@mui/material";
import SportsTennisIcon from "@mui/icons-material/SportsTennis";
import { getCurrentUser } from "../../../utils/getCurrentUser";
import axios from "axios";

const Navigation = () => {
  const navigate = useNavigate();
  const user = getCurrentUser();

  const logout = async () => {
    try {
      await axios.get("/api/v1/auth/logout");
      localStorage.clear();
      navigate("/login");
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar sx={{ backgroundColor: "#FF4081" }} position="static">
          <Toolbar>
            <IconButton
              onClick={() => navigate("/")}
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <SportsTennisIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Tennis
            </Typography>
            {Boolean(user) && (
              <Button color="inherit" onClick={logout}>
                Logout
              </Button>
            )}
          </Toolbar>
        </AppBar>
      </Box>
    </>
  );
};

export default Navigation;
