import { Paper, Typography, Grid } from "@mui/material";
import { FC } from "react";
import { PlayerInTeam } from "../../../services/playersService";
import TeamPlayerBalls from "../TeamPlayerBalls/TeamPlayerBalls";

export type TeamPlayerCardProps = {
  player: PlayerInTeam;
  index: number;
};

const TeamPlayerCard: FC<TeamPlayerCardProps> = ({ player, index }) => {
  return (
    <>
      <Grid container>
        <Grid item xs={12} display="flex" justifyContent="center">
          <Paper sx={{ backgroundColor: "#BAC0BE", p: 1, width: "131px" }}>
            <Grid sx={{ mb: 2 }} container>
              <Grid item xs={3} display="flex" justifyContent="start">
                <Typography
                  sx={{
                    display: "flex",
                    alignItems: "start",
                  }}
                  align="left"
                  variant="button"
                  display="block"
                >
                  <strong>{index}</strong>
                </Typography>
              </Grid>
              <Grid item xs={9}>
                <Paper
                  sx={{
                    backgroundColor: "#02563C",

                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <Typography variant="button" display="block">
                    <strong style={{ color: "#FFF" }}>
                      {player.pointsWon.toFixed(1)}PT
                    </strong>
                  </Typography>
                </Paper>
              </Grid>
            </Grid>
            <img
              width={82}
              height={82}
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                margin: "auto",
                border: "2px solid #02563C",
                borderRadius: "50%",
              }}
              src={player.imageUrl}
              alt="player"
            />
            <Typography
              align="center"
              sx={{
                mt: 1,
                fontSize: "12px",
              }}
              variant="button"
              display="block"
            >
              <strong>
                {player.name.split(" ")[1].length > 10
                  ? player.name.split(" ")[1].substring(0, 10).toUpperCase() +
                    "..."
                  : player.name.split(" ")[1].toUpperCase()}
              </strong>
            </Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} display="flex" justifyContent="center">
          <TeamPlayerBalls player={player} />
        </Grid>
      </Grid>
    </>
  );
};

export default TeamPlayerCard;
