import React, { FC } from "react";
import {
  Card,
  CardContent,
  CardMedia,
  Chip,
  Grid,
  Stack,
  Typography,
} from "@mui/material";

type LastMatchCardProps = {
  date: string;
  awayPlayer: {
    id: number;
    name: string;
    country: string;
  };
  awaySets: string;
  homePlayer: {
    id: number;
    name: string;
    country: string;
  };
  homeSets: string;
  winnerId: number;
  playerId?: number;
};

const LastMatchCard: FC<LastMatchCardProps> = ({
  date,
  awayPlayer,
  awaySets,
  homePlayer,
  homeSets,
  winnerId,
  playerId,
}) => {
  return (
    <Grid
      container
      alignItems="center"
      sx={{ borderBottom: "1px solid rgb(0 0 0 / 20%)", marginTop: "8px" }}
    >
      <Grid item xs={3}>
        <Typography variant="body2">
          {new Date(date).toLocaleDateString("de-DE", {
            day: "numeric",
            month: "numeric",
            year: "numeric",
          })}
        </Typography>
      </Grid>
      <Grid item xs={7}>
        <Stack gap={1}>
          <Card
            sx={{
              display: "flex",
              borderRadius: 0,
              boxShadow: "none",
              cursor: "pointer",
            }}
          >
            <Grid container alignItems="center">
              <Grid item xs={3}>
                <CardMedia
                  component="img"
                  sx={{
                    maxWidth: 150,
                    height: "auto",
                    objectFit: "contain",
                    boxShadow:
                      "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
                  }}
                  crossOrigin="anonymous"
                  src={`https://countryflagsapi.com/png/${homePlayer.country.toLowerCase()}`}
                  alt={homePlayer.country}
                />
              </Grid>
              <Grid item xs={7}>
                <CardContent sx={{ flexGrow: "1" }}>
                  <Typography
                    component="div"
                    variant="body2"
                    fontWeight={winnerId === homePlayer.id ? "800" : "100"}
                  >
                    {homePlayer.name}
                  </Typography>
                </CardContent>
              </Grid>
              <Grid item xs={2}>
                <CardContent>
                  <Typography
                    component="div"
                    variant="body2"
                    fontWeight={winnerId === homePlayer.id ? "800" : "100"}
                  >
                    {homeSets}
                  </Typography>
                </CardContent>
              </Grid>
            </Grid>
          </Card>
          <Card
            sx={{
              display: "flex",
              borderRadius: 0,
              boxShadow: "none",
              cursor: "pointer",
            }}
          >
            <Grid container alignItems="center">
              <Grid item xs={3}>
                <CardMedia
                  component="img"
                  sx={{
                    maxWidth: 150,
                    height: "auto",
                    objectFit: "contain",
                    boxShadow:
                      "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
                  }}
                  crossOrigin="anonymous"
                  src={`https://countryflagsapi.com/png/${awayPlayer.country.toLowerCase()}`}
                  alt={awayPlayer.country}
                />
              </Grid>
              <Grid item xs={7}>
                <CardContent sx={{ flexGrow: "1" }}>
                  <Typography
                    component="div"
                    variant="body2"
                    fontWeight={winnerId === awayPlayer.id ? "800" : "100"}
                  >
                    {awayPlayer.name}
                  </Typography>
                </CardContent>
              </Grid>
              <Grid item xs={2}>
                <CardContent>
                  <Typography
                    component="div"
                    variant="body2"
                    fontWeight={winnerId === awayPlayer.id ? "800" : "100"}
                  >
                    {awaySets}
                  </Typography>
                </CardContent>
              </Grid>
            </Grid>
          </Card>
        </Stack>
      </Grid>
      {playerId && (
        <Grid item xs={2}>
          <Chip
            label={winnerId === playerId ? "W" : "L"}
            color={winnerId === playerId ? "success" : "error"}
          />
        </Grid>
      )}
    </Grid>
  );
};

export default LastMatchCard;
