import { FC } from "react";
import { useNavigate } from "react-router-dom";

import {
  Box,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Grid,
  Typography,
} from "@mui/material";

type TournamentCardProps = {
  id: number;
  tournamentName: string;
  countryName: string;
  tournamentCode: string;
  image: string;
  favorites: number;
  matches: number;
  date: string;
};

const TournamentCard: FC<TournamentCardProps> = ({
  id,
  tournamentName,
  countryName,
  tournamentCode,
  image,
  favorites,
  matches,
  date,
}) => {
  const navigate = useNavigate();
  return (
    <Card
      onClick={() => navigate(`/tournaments/${id}/matches/byDate/${date}`)}
      sx={{
        display: "flex",
        borderRadius: 0,
        boxShadow: "none",
        borderBottom: "1px solid rgb(0 0 0 / 20%)",
        cursor: "pointer",
      }}
    >
      <Box
        component="div"
        sx={{ flexBasis: "30%", display: "flex", alignItems: "center" }}
      >
        <CardMedia
          component="img"
          sx={{
            width: 150,
            height: "auto",
            objectFit: "contain",
            boxShadow:
              "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
          }}
          crossOrigin="anonymous"
          src={image}
          alt={countryName}
        />
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          flexBasis: "70%",
        }}
      >
        <CardContent sx={{ flexGrow: "1" }}>
          <Grid
            container
            alignItems="center"
            justifyContent="space-between"
            sx={{ columnGap: "10px" }}
          >
            <Grid item sm={7} md={7} lg={7}>
              <Typography component="div" variant="h5">
                {countryName}
              </Typography>
              <Typography component="div" variant="subtitle1" color="secondary">
                {tournamentName} {tournamentCode}
              </Typography>
            </Grid>
            <Grid
              item
              sm={4}
              md={4}
              lg={4}
              sx={{ display: "flex", gap: "15px", justifyContent: "flex-end" }}
            >
              <Chip color="error" label={favorites} />
              <Chip label={matches} />
            </Grid>
          </Grid>
        </CardContent>
      </Box>
    </Card>
  );
};

export default TournamentCard;
