import React, { FC, useState, SyntheticEvent } from "react";

import MatchSummary from "./MatchSummary";
import MatchStats from "./MatchStats";
import MatchHeadToHead from "./MatchHeadToHead";
import MatchDraw from "./MatchDraw";

import { Box, Tab, Tabs } from "@mui/material";
import { Match } from "../../../services/matchesService";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ marginTop: "15px" }}>{children}</Box>}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

type MatchDetailTabsProps = {
  match: Match;
};

const MatchDetailTabs: FC<MatchDetailTabsProps> = ({ match }) => {
  const [value, setValue] = useState(0);

  const handleChange = (event: SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const summary = {
    ...match,
  };

  const headToHead = {
    id: match.id,
    homeId: match.homeId,
    awayId: match.awayId,
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box
        sx={{
          borderBottom: 1,
          borderColor: "divider",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Tabs
          variant="scrollable"
          scrollButtons="auto"
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="SUMMARY" {...a11yProps(0)} />
          <Tab label="STATS" {...a11yProps(1)} />
          <Tab label="H2H" {...a11yProps(2)} />
          <Tab label="DRAW" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <MatchSummary summary={summary} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <MatchStats />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <MatchHeadToHead {...headToHead} />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <MatchDraw />
      </TabPanel>
    </Box>
  );
};

export default MatchDetailTabs;
