import { Typography } from "@mui/material";
import React from "react";
import { useParams } from "react-router-dom";
import MatchList from "../../components/containers/MatchList/MatchList";
import SingleTournamentCard from "../../components/containers/TournamentList/SingleTournamentCard";
import {
  Tournament,
  useSingleTournament,
} from "../../services/tournamentsService";

const SingleTournamentMatches = () => {
  const { id: tournamentId } = useParams();

  const { data, isLoading, isError } = useSingleTournament(tournamentId);

  if (isLoading) {
    return <Typography>Loading...</Typography>;
  }

  if (isError) {
    return <Typography>Error ocurred</Typography>;
  }

  const { id, name, countryName, code, surface } =
    data?.tournament as Tournament;

  return (
    <>
      <SingleTournamentCard
        id={Number(id)}
        name={name}
        code={code}
        countryName={countryName}
        surface={surface}
      />
      <MatchList />
    </>
  );
};

export default SingleTournamentMatches;
