import { Grid } from "@mui/material";
import BracketLink from "../../components/containers/BracketLink/BracketLink";
import Dashboard from "../../components/containers/Dashboard/Dashboard";
import FantasyGameLink from "../../components/containers/FantasyGameLink/FantasyGameLink";
import PredictionsLink from "../../components/containers/PredictionsLink/PredictionsLink";

const Games = () => {
  return (
    <div>
      <FantasyGameLink />
      <Grid sx={{ mt: 2 }} spacing={2} container>
        <Grid item xs={6}>
          <PredictionsLink />
        </Grid>
        <Grid item xs={6}>
          <BracketLink />
        </Grid>
      </Grid>
      <Dashboard />
    </div>
  );
};

export default Games;
