import { useState } from "react";
import { useAtom } from "jotai";
import { useTournamentsByWeek } from "../../../services/tournamentsService";
import { selectedWeekAtom } from "../../../utils/atoms";

import { Paper, Grid, IconButton, Divider, Chip, Button } from "@mui/material";

import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import { useAllRounds } from "../../../services/roundsService";
import {
  Bracket,
  useAllBracketsByTournamentAndRound,
} from "../../../services/bracketsService";
import BracketsContainer from "../BracketsContainer/BracketsContainer";

const TournamentsCarousel = () => {
  const [selected] = useAtom(selectedWeekAtom);
  const [current, setCurrent] = useState(0);
  const [roundId, setRoundId] = useState("63a43db0416fe2a5d31c9a31");

  const { data } = useTournamentsByWeek(selected.value);
  const { data: roundsData } = useAllRounds();
  const { data: bracketsData } = useAllBracketsByTournamentAndRound(
    Number(data?.tournaments[current]?.id),
    roundId
  );

  return (
    <>
      <Paper sx={{ mt: 3, backgroundColor: "#D3D3D3", width: "100%" }}>
        <Grid container>
          <Grid
            item
            xs={1}
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            <IconButton>
              <KeyboardArrowLeftIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid
            item
            xs={10}
            display="flex"
            alignItems="center"
            justifyContent="center"
            gap={1}
          >
            <Grid item xs={2} display="flex" alignItems="center">
              <img
                crossOrigin="anonymous"
                style={{ maxWidth: "100%", height: "auto" }}
                src={`${data && data?.tournaments[current]?.countryFlag}`}
                alt="country flag"
              />
            </Grid>
            <Grid item xs={8} display="flex" alignItems="center">
              <strong style={{ color: "#545D84", fontSize: "15px" }}>
                {data && data?.tournaments[current]?.name.toUpperCase()}{" "}
              </strong>
            </Grid>
          </Grid>
          <Grid
            item
            xs={1}
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            <IconButton>
              <KeyboardArrowRightIcon fontSize="large" />
            </IconButton>
          </Grid>
        </Grid>
      </Paper>

      <Divider sx={{ mt: 3 }} />

      <Grid container sx={{ mt: 3 }}>
        {roundsData &&
          roundsData.rounds.map((round) => (
            <Grid
              key={round._id}
              item
              xs={4}
              display="flex"
              justifyContent="center"
            >
              <Button onClick={() => setRoundId(round._id)}>
                <Chip
                  label={round.name}
                  sx={{
                    backgroundColor: "#02563C",
                    color: "#FFF",
                    mb: 2,
                    ml: 1,
                  }}
                />
              </Button>
            </Grid>
          ))}
      </Grid>

      <Divider sx={{ mt: 3 }} />

      <BracketsContainer brackets={bracketsData?.brackets as Bracket[]} />
    </>
  );
};

export default TournamentsCarousel;
