import { FC } from "react";
import { Bracket } from "../../../services/bracketsService";

import { Paper, Grid, Typography, IconButton } from "@mui/material";
import { useMatchPlayer } from "../../../services/playersService";

import RadioButtonUncheckedIcon from "@mui/icons-material/RadioButtonUnchecked";
import CheckIcon from "@mui/icons-material/Check";
import {
  CreatePickInputModel,
  useCheckIfPickHasBeenMade,
  useCreatePick,
} from "../../../services/picksService";

export type BracketsCardProps = {
  bracket: Bracket;
};

const BracketsCard: FC<BracketsCardProps> = ({ bracket }) => {
  const { data: homePlayer } = useMatchPlayer(
    bracket.homeId?.toString() as string
  );
  const { data: awayPlayer } = useMatchPlayer(
    bracket.awayId?.toString() as string
  );
  const { data: checkForPick } = useCheckIfPickHasBeenMade(bracket._id);
  const { mutate: createPick } = useCreatePick();

  const handleCreatePick = (inputModel: CreatePickInputModel) => {
    createPick(inputModel);
  };

  return (
    <div style={{ marginTop: "10px" }}>
      <Typography variant="h6" align="center">
        {bracket.name}
      </Typography>
      <Grid container>
        <Grid item xs={1}></Grid>
        <Grid item xs={10} display="flex" justifyContent="center">
          <Paper
            sx={{
              backgroundColor: "#D3D3D3",
              width: "100%",
              p: 1,
              minHeight: !homePlayer && !awayPlayer ? "60px" : "",
            }}
          >
            <Typography gutterBottom align="center">
              {homePlayer && homePlayer.player.id === bracket.winnerId ? (
                <strong>
                  {homePlayer &&
                    homePlayer.player.name + ` (${homePlayer.player.ranking})`}
                </strong>
              ) : (
                homePlayer &&
                homePlayer.player.name + ` (${homePlayer.player.ranking})`
              )}
              {checkForPick?.haveBeenMade ? (
                checkForPick.pick.playerId === homePlayer?.player.id ? (
                  <IconButton>
                    <CheckIcon color="success" />
                  </IconButton>
                ) : (
                  <IconButton disabled>
                    <RadioButtonUncheckedIcon />
                  </IconButton>
                )
              ) : (
                homePlayer && (
                  <IconButton
                    onClick={() =>
                      handleCreatePick({
                        bracketId: bracket._id,
                        playerId: Number(homePlayer?.player.id),
                      })
                    }
                  >
                    <RadioButtonUncheckedIcon />
                  </IconButton>
                )
              )}
            </Typography>
            <Typography align="center">
              {awayPlayer && awayPlayer.player.id === bracket.winnerId ? (
                <strong>
                  {awayPlayer &&
                    awayPlayer.player.name + ` (${awayPlayer.player.ranking})`}
                </strong>
              ) : (
                awayPlayer &&
                awayPlayer.player.name + ` (${awayPlayer.player.ranking})`
              )}
              {checkForPick?.haveBeenMade ? (
                checkForPick.pick.playerId === awayPlayer?.player.id ? (
                  <IconButton>
                    <CheckIcon color="success" />
                  </IconButton>
                ) : (
                  <IconButton disabled>
                    <RadioButtonUncheckedIcon />
                  </IconButton>
                )
              ) : (
                awayPlayer && (
                  <IconButton
                    onClick={() =>
                      handleCreatePick({
                        bracketId: bracket._id,
                        playerId: Number(awayPlayer?.player.id),
                      })
                    }
                  >
                    <RadioButtonUncheckedIcon />
                  </IconButton>
                )
              )}
            </Typography>
          </Paper>
        </Grid>
        <Grid item xs={1}></Grid>
      </Grid>
    </div>
  );
};

export default BracketsCard;
