import { useAtom } from "jotai";
import { selectedWeekAtom } from "../../../utils/atoms";
import { Button } from "@mui/material";
import { useCalculateWeekly } from "../../../services/playersService";

const CalculateWeekly = () => {
  const [selected] = useAtom(selectedWeekAtom);
  const { mutate: calculateWeekly } = useCalculateWeekly();

  const calculateWeeklyHandler = () => calculateWeekly(selected.value);

  return (
    <Button
      aria-label="open drawer"
      onClick={calculateWeeklyHandler}
      variant="contained"
      sx={{
        display: "flex",
        justifyContent: "center",
        margin: "auto",
        mt: 3,
        backgroundColor: "#02563C !important",
        color: "#FFF",
        borderRadius: "20px",
      }}
    >
      UPDATE WEEKLY POINTS
    </Button>
  );
};

export default CalculateWeekly;
