import { FC } from "react";

import { Paper, Grid, Divider, IconButton, Typography } from "@mui/material";
import {
  Player,
  useAddPlayerToTeam,
  useDeletePlayerFromTeam,
  usePlayersInTeam,
} from "../../../services/playersService";

import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import { selectedWeekAtom } from "../../../utils/atoms";
import { useAtom } from "jotai";

export type PlayerCardProps = {
  player: Player;
};

const PlayerCard: FC<PlayerCardProps> = ({ player }) => {
  const [selected] = useAtom(selectedWeekAtom);

  const { data, isLoading } = usePlayersInTeam({ selected });

  const { mutate: addPlayerToTeam } = useAddPlayerToTeam();
  const { mutate: deletePlayerFromTeam } = useDeletePlayerFromTeam();

  const isPlayerBought = () => {
    return !isLoading && data?.players.some((p) => p.id === player.id);
  };

  const addPlayer = () => {
    const inputModel = { weekId: selected.value, playerId: player.id };
    addPlayerToTeam(inputModel);
  };

  const deletePlayer = () => {
    const inputModel = { weekId: selected.value, playerId: player.id };
    deletePlayerFromTeam(inputModel);
  };

  return (
    <>
      <Paper sx={{ mt: 1 }}>
        <Grid container>
          <Grid item xs={8} display="flex" alignItems="center">
            <Grid item xs={5}>
              <img width={42} height={42} src={player.imageUrl} alt="player" />
            </Grid>
            <Grid item xs={7}>
              <Typography variant="button" display="block" gutterBottom>
                <strong>{player.name}</strong>
              </Typography>
              <Typography
                sx={{ color: "#555E6B" }}
                variant="button"
                display="block"
                gutterBottom
              >
                {player.gender === "male"
                  ? `ATP ${player.ranking}`
                  : `WTA ${player.ranking}`}
              </Typography>
            </Grid>
          </Grid>
          <Divider orientation="vertical" flexItem />
          <Grid item xs={3}>
            <Grid
              item
              xs={12}
              display="flex"
              justifyContent="center"
              sx={{ mt: 1 }}
            >
              <Typography variant="button" display="block" gutterBottom>
                <strong>{player.price / 1000000}M</strong>
              </Typography>
            </Grid>
            <Grid item xs={12} display="flex" justifyContent="center">
              {!isPlayerBought() ? (
                <IconButton onClick={addPlayer} aria-label="delete">
                  <AddCircleOutlineIcon
                    fontSize="large"
                    sx={{ color: "#D76316" }}
                  />
                </IconButton>
              ) : (
                <IconButton onClick={deletePlayer} aria-label="delete">
                  <RemoveCircleOutlineIcon fontSize="large" />
                </IconButton>
              )}
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
};

export default PlayerCard;
