import { useQuery } from "react-query";
import axios from "axios";

export type Bracket = {
  _id: string;
  name: string;
  homeId?: number;
  awayId?: number;
  winnerId?: number;
  roundId: string;
  tournamentId: number;
  connectedBracketId: string;
};

export type GetAllBracketsByTournamentAndRoundViewModel = {
  brackets: Bracket[];
};

// React Query Hooks

export function useAllBracketsByTournamentAndRound(
  tournamentId: number,
  roundId: string
) {
  return useQuery(["brackets", tournamentId, roundId], () =>
    getAllBracketsByTournamentAndRound(tournamentId, roundId)
  );
}

// API methods

export const getAllBracketsByTournamentAndRound = async (
  tournamentId: number,
  roundId: string
) => {
  const response = await axios.get<GetAllBracketsByTournamentAndRoundViewModel>(
    `/api/v1/brackets/byTournamentAndRound?tournamentId=${tournamentId}&roundId=${roundId}`
  );
  const result = response.data;

  return result;
};
