import { useQuery } from "react-query";
import axios from "axios";

export type Round = {
  _id: string;
  name: string;
};

export type GetAllRoundsViewModel = {
  rounds: Round[];
};

// React Query Hooks

export function useAllRounds() {
  return useQuery(["brackets"], () => getAllRounds());
}

// API methods

export const getAllRounds = async () => {
  const response = await axios.get<GetAllRoundsViewModel>(`/api/v1/rounds`);
  const result = response.data;

  return result;
};
