import { FC } from "react";

import { Paper, Typography } from "@mui/material";

export type TotalPointsProps = {
  points: number;
};

const TotalPoints: FC<TotalPointsProps> = ({ points }) => {
  return (
    <>
      <Paper
        sx={{
          backgroundColor: "#D76316",
          width: "100px",
          height: "100px",
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          margin: "auto",
        }}
      >
        <Typography
          sx={{
            color: "#FFF",
            fontSize: "22px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            margin: "auto",
          }}
          variant="button"
          display="block"
        >
          <strong>{points}</strong>
        </Typography>
      </Paper>

      <Typography
        align="center"
        sx={{
          mt: 2,
          color: "#02563C",
          fontSize: "15px",
        }}
        variant="button"
        display="block"
      >
        <strong>TOTAL POINTS</strong>
      </Typography>
    </>
  );
};

export default TotalPoints;
