import { FC, useState } from "react";
import { Button, Menu, MenuItem } from "@mui/material";

import ArrowDownwardIcon from "@mui/icons-material/ArrowDownward";
import { useAtom } from "jotai";
import { selectedWeekAtom } from "../../../utils/atoms";

export type Item = {
  text: string;
  value: string;
};

export type ItemsMenuProps = {
  options: Item[];
};

const ITEM_HEIGHT = 48;

const ItemsMenu: FC<ItemsMenuProps> = ({ options }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [selected, setSelected] = useAtom(selectedWeekAtom);

  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (selected: Item) => {
    setSelected(selected);
    setAnchorEl(null);
  };

  return (
    <div>
      <Button
        sx={{
          color: "#FFF",
          display: "flex",
          justifyContent: "center",
          margin: "auto",
        }}
        aria-label="more"
        id="long-button"
        aria-controls={open ? "long-menu" : undefined}
        aria-expanded={open ? "true" : undefined}
        aria-haspopup="true"
        onClick={handleClick}
      >
        {options && selected?.text}
        <ArrowDownwardIcon />
      </Button>
      <Menu
        id="long-menu"
        MenuListProps={{
          "aria-labelledby": "long-button",
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={() => handleClose(selected)}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: "20ch",
          },
        }}
      >
        {options?.map((option) => (
          <MenuItem key={option.value} onClick={() => handleClose(option)}>
            {option.text}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
};

export default ItemsMenu;
