import { FC } from "react";
import { useNavigate } from "react-router-dom";

import {
  Box,
  Card,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@mui/material";

type SingleTournamentCardProps = {
  id: number;
  name: string;
  countryName: string;
  code: string;
  surface: string;
};

const SingleTournamentCard: FC<SingleTournamentCardProps> = ({
  id,
  name,
  countryName,
  code,
  surface,
}) => {
  const navigate = useNavigate();
  return (
    <Card
      onClick={() => navigate(`/tournaments/${id}`)}
      sx={{
        display: "flex",
        borderRadius: 0,
        boxShadow: "none",
        borderBottom: "1px solid rgb(0 0 0 / 20%)",
        background: "#d3d3d3",
        padding: "16px",
        cursor: "pointer",
      }}
    >
      <Box
        component="div"
        sx={{ flexBasis: "30%", display: "flex", alignItems: "center" }}
      >
        <CardMedia
          component="img"
          sx={{
            width: 150,
            height: "auto",
            objectFit: "contain",
            boxShadow:
              "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
          }}
          crossOrigin="anonymous"
          src={`https://countryflagsapi.com/png/${countryName.toLowerCase()}`}
          alt={countryName}
        />
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          flexBasis: "70%",
        }}
      >
        <CardContent sx={{ flexGrow: "1" }}>
          <Grid
            container
            alignItems="center"
            justifyContent="space-between"
            sx={{ columnGap: "10px" }}
          >
            <Grid item sm={7} md={7} lg={7}>
              <Typography
                fontWeight="bolder"
                component="div"
                variant="h5"
                color="secondary"
              >
                {name} {code}, {surface}
              </Typography>
              <Typography component="div" variant="subtitle1">
                {countryName}
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
      </Box>
    </Card>
  );
};

export default SingleTournamentCard;
