import { FC } from "react";
import { Paper, Typography } from "@mui/material";

export type DashboardPointsProps = {
  points: number;
  type: string;
  color: string;
};

const DashboardPoints: FC<DashboardPointsProps> = ({ points, type, color }) => {
  return (
    <>
      <Paper
        sx={{
          backgroundColor: "#FFF",
          width: "70px",
          height: "70px",
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          margin: "auto",
          border: `2px solid ${color}`,
        }}
      >
        <Typography
          sx={{
            color: color,
            fontSize: "15px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            margin: "auto",
          }}
          variant="button"
          display="block"
        >
          <strong>{points}</strong>
        </Typography>
      </Paper>

      <Typography
        align="center"
        sx={{
          mt: 2,
          color: color,
          fontSize: "15px",
        }}
        variant="button"
        display="block"
      >
        <strong>{type}</strong>
      </Typography>
    </>
  );
};

export default DashboardPoints;
