import React, { FC } from "react";
import { Box, Typography } from "@mui/material";
import { MatchPlayer, useMatchPlayer } from "../../../services/playersService";

type MatchPlayerCardProps = {
  id: number;
};

const MatchPlayerCard: FC<MatchPlayerCardProps> = ({ id }) => {
  const { data, isLoading, isError } = useMatchPlayer(`${id}`);

  if (isLoading) {
    return <Typography>Loading... </Typography>;
  }

  if (isError || !data) {
    return <Typography>Error ocurred</Typography>;
  }

  const { name, country, gender, imageUrl, ranking } =
    data?.player as MatchPlayer;

  return (
    <Box
      sx={{
        width: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <img
        crossOrigin="anonymous"
        src={imageUrl}
        style={{
          width: "90%",
          maxWidth: "150px",
          height: "auto",
        }}
        alt={name}
      />
      <Typography textAlign="center" variant="caption">
        {name} ({country})
      </Typography>
      <Typography textAlign="center" variant="caption">
        {gender === "male" ? `ATP: ${ranking}` : `WTA: ${ranking}`}
      </Typography>
    </Box>
  );
};

export default MatchPlayerCard;
