import axios from "axios";
import {useQuery} from "react-query";
import { MatchPlayer } from "./playersService";
import { Tournament } from "./tournamentsService";

export type Match = {
    id: number;
    status: string;
    homeId: number;
    homePlayer: MatchPlayer,
    awayId: number;
    awayPlayer: MatchPlayer,
    date: string;
    round: string;
    winnerId: number;
    homeSets: string;
    awaySets: string;
    homeSet1: string;
    homeSet2:string;
    homeSet3: string;
    homeSet4: string;
    homeSet5: string;
    awaySet1: string;
    awaySet2: string;
    awaySet3: string;
    awaySet4: string;
    awaySet5: string;
    tournamentId: number;
};

export type HeadToHeadMatchViewModel =  {
    id: number;
    player: MatchPlayer
    homePlayer: MatchPlayer,
    awayPlayer: MatchPlayer,
    homeSets: string,
    awaySets: string,
    winnerId: number,
    date: string,
    tournament: Tournament,
};

export type SingleMatchViewModel = {
    match: Match,
};

export function useSingleMatch(id?: string){
    return useQuery(["matches", id], getSingleMatch);
}

const getSingleMatch = async ({queryKey}: {queryKey: (string | undefined)[]}) => {
    const response = await axios.get<SingleMatchViewModel>(`/api/v1/matches/${queryKey[1]}`);
    const result = response.data;

    return result;
}