import { Paper, Grid, Typography, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

const PredictionsLink = () => {
  const navigate = useNavigate();

  return (
    <>
      <Paper
        sx={{
          background:
            "url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQyMB858SjXAnDMi6udhGl5-cVTdrPEnyEETQ&usqp=CAU)",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
          p: 3,
        }}
      >
        <Grid container>
          <Grid item xs={12} display="flex" justifyContent="center">
            <Typography
              sx={{
                color: "#FFF",
                fontSize: "18px",
                mt: 2,
                fontFamily: '"Kaushan Script", cursive',
              }}
              align="center"
            >
              <strong>PREDICT</strong>
            </Typography>
          </Grid>
          <Grid item xs={12} display="flex" justifyContent="center">
            <Typography
              sx={{
                color: "#FFF",
                fontSize: "12px",
                mt: 2,
                fontFamily: '"Kaushan Script", cursive',
              }}
              align="center"
            >
              <em>GIVE YOUR BOLD PREDICTIONS</em>
            </Typography>
          </Grid>
        </Grid>
      </Paper>
      <Button
        onClick={() => navigate("/")}
        sx={{ backgroundColor: "#02563C", color: "#FFF", width: "100%" }}
        variant="contained"
      >
        PLAY
      </Button>
    </>
  );
};

export default PredictionsLink;
