import { BrowserRouter, Routes, Route } from "react-router-dom";
import MainLayout from "./components/layouts/MainLayout/MainLayout";
import ProtectedRoutes from "./components/shared/ProtectedRoutes/ProtectedRoutes";
import Brackets from "./pages/Brackets/Brackets";
import Games from "./pages/Games/Games";
import Home from "./pages/Home/Home";
import Login from "./pages/Login/Login";
import MatchDetails from "./pages/Match/MatchDetails";
import Register from "./pages/Register/Register";
import Team from "./pages/Team/Team";
import SingleTournamentMatches from "./pages/Tournament/SingleTournamentMatches";
import Tournaments from "./pages/Tournament/Tournaments";

const App = () => {
  return (
    <BrowserRouter>
      <MainLayout>
        <Routes>
          <Route path="register" element={<Register />} />
          <Route path="login" element={<Login />} />
          <Route element={<ProtectedRoutes />}>
            <Route path="/" element={<Home />}>
              <Route index element={<Tournaments />} />
              <Route
                path="tournaments/:id/matches/byDate/:date"
                element={<SingleTournamentMatches />}
              />
              <Route path="matches/:id" element={<MatchDetails />} />
              <Route path="games" element={<Games />} />
              <Route path="team" element={<Team />} />
              <Route path="brackets" element={<Brackets />} />
            </Route>
          </Route>
        </Routes>
      </MainLayout>
    </BrowserRouter>
  );
};

export default App;
