import {
  Paper,
  BottomNavigation as BottomNavigationComponent,
  BottomNavigationAction,
  Box,
} from "@mui/material";
import { useState } from "react";

import SportsTennisIcon from "@mui/icons-material/SportsTennis";
import SportsEsportsIcon from "@mui/icons-material/SportsEsports";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import EmojiEventsIcon from "@mui/icons-material/EmojiEvents";
import { useNavigate } from "react-router-dom";

const BottomNavigation = () => {
  const navigate = useNavigate();

  const [value, setValue] = useState(0);

  return (
    <Box sx={{ pb: 7 }}>
      <Paper
        sx={{
          position: "fixed",
          bottom: 0,
          left: 0,
          right: 0,
        }}
        elevation={3}
      >
        <BottomNavigationComponent
          sx={{ backgroundColor: "#FF4081" }}
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        >
          <BottomNavigationAction
            onClick={() => navigate("/")}
            icon={<SportsTennisIcon sx={{ color: "#FFF" }} />}
          />
          <BottomNavigationAction
            onClick={() => navigate("/games")}
            icon={<SportsEsportsIcon sx={{ color: "#FFF" }} />}
          />
          <BottomNavigationAction
            icon={<StarBorderIcon sx={{ color: "#FFF" }} />}
          />
          <BottomNavigationAction
            icon={<EmojiEventsIcon sx={{ color: "#FFF" }} />}
          />
        </BottomNavigationComponent>
      </Paper>
    </Box>
  );
};

export default BottomNavigation;
