import { Grid } from "@mui/material";
import { useGetTotal } from "../../../services/picksService";
import TotalBracketPoints from "../TotalBracketPoints/TotalBracketPoints";
import WeeklyBracketPoints from "../WeeklyBracketPoints/WeeklyBracketPoints";

const BracketPointsContainer = () => {
  const { data: totalBracketPoints, isLoading: isLoadingTotal } = useGetTotal();

  return (
    <>
      <Grid sx={{ mt: 3 }} container>
        <Grid item xs={6}>
          <WeeklyBracketPoints />
        </Grid>
        <Grid item xs={6}>
          <TotalBracketPoints
            points={isLoadingTotal ? 0 : Number(totalBracketPoints)}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default BracketPointsContainer;
