import React from "react";
import { useSingleTournamentMatches } from "../../../services/tournamentsService";
import { useParams } from "react-router-dom";
import { Typography, Paper } from "@mui/material";
import MatchCard from "./MatchCard";

const MatchList = () => {
  const { id: tournamentId, date } = useParams();

  const { data, isLoading, isError } = useSingleTournamentMatches(
    tournamentId,
    date
  );

  if (isLoading) {
    return <Typography>Loading...</Typography>;
  }

  if (isError) {
    return <Typography>Error ocurred</Typography>;
  }

  if (data?.matches.length === 0) {
    return (
      <Typography variant="h4" textAlign="center">
        No matches matching the date
      </Typography>
    );
  }

  return (
    <>
      {data?.matches.map((m) => {
        return (
          <MatchCard
            key={m.id}
            id={m.id}
            homePlayer={m.homePlayer}
            homePlayerSets={m.homeSets}
            awayPlayer={m.awayPlayer}
            awayPlayerSets={m.awaySets}
            winnerId={m.winnerId}
          />
        );
      })}
    </>
  );
};

export default MatchList;
