import { Grid } from "@mui/material";
import WeeklyPoints from "../WeeklyPoints/WeeklyPoints";
import TotalPoints from "../TotalPoints/TotalPoints";
import { useGetTotal } from "../../../services/playersService";

const PointsContainer = () => {
  const { data: totalPoints, isLoading: isLoadingTotal } = useGetTotal();

  return (
    <>
      <Grid sx={{ mt: 3 }} container>
        <Grid item xs={6}>
          <WeeklyPoints />
        </Grid>
        <Grid item xs={6}>
          <TotalPoints points={isLoadingTotal ? 0 : Number(totalPoints)} />
        </Grid>
      </Grid>
    </>
  );
};

export default PointsContainer;
