import { Paper, Grid, Button, Divider } from "@mui/material";

import GroupIcon from "@mui/icons-material/Group";
import EmojiEventsIcon from "@mui/icons-material/EmojiEvents";
import ConnectWithoutContactIcon from "@mui/icons-material/ConnectWithoutContact";
import ItemsMenu from "../../shared/ItemsMenu/ItemsMenu";
import { useWeeks } from "../../../services/weeksService";

const TeamMenu = () => {
  const { data: weeksObject, isLoading } = useWeeks();

  const options =
    !isLoading && weeksObject
      ? weeksObject?.weeks?.map((week) => {
          const option = {
            text: week.name,
            value: week._id,
          };
          return option;
        })
      : [{ text: "GW1", value: "63a0343ad7fba86bf211d965" }];

  return (
    <Paper sx={{ backgroundColor: "#02563C", mt: 2 }}>
      <Grid container>
        <Grid item xs={4} display="flex" justifyContent="center">
          <Button sx={{ backgroundColor: "#02563C" }}>
            <GroupIcon sx={{ color: "#FFF" }} fontSize="large" />
          </Button>
        </Grid>
        <Divider
          sx={{ backgroundColor: "#FFF" }}
          orientation="vertical"
          variant="middle"
          flexItem
        />
        <Grid item xs={4} display="flex" justifyContent="center">
          <Button sx={{ backgroundColor: "#02563C" }}>
            <EmojiEventsIcon sx={{ color: "#FFF" }} fontSize="large" />
          </Button>
        </Grid>
        <Divider
          sx={{ backgroundColor: "#FFF" }}
          orientation="vertical"
          variant="middle"
          flexItem
        />
        <Grid item xs={3} display="flex" justifyContent="center">
          <Button sx={{ backgroundColor: "#02563C" }}>
            <ConnectWithoutContactIcon
              sx={{ color: "#FFF" }}
              fontSize="large"
            />
          </Button>
        </Grid>
      </Grid>

      <Divider sx={{ backgroundColor: "#D76316", height: "5px", mb: 1 }} />

      <ItemsMenu options={options} />
    </Paper>
  );
};

export default TeamMenu;
