import { ChangeEvent } from "react";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Grid,
} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import { useAtom } from "jotai";
import {
  currentPageAtom,
  fetchPlayersAtom,
  isBoughtAtom,
  playerSearchItemAtom,
} from "../../../utils/atoms";

const SearchPlayer = () => {
  const [, setPlayerSearchItem] = useAtom(playerSearchItemAtom);
  const [checked, setChecked] = useAtom(isBoughtAtom);
  const [, setFetchPlayers] = useAtom(fetchPlayersAtom);
  const [, setPage] = useAtom(currentPageAtom);

  return (
    <>
      <FormControl
        sx={{
          m: 1,
          width: "25ch",
        }}
        variant="standard"
      >
        <InputLabel htmlFor="standard-adornment-search-player">
          Search
        </InputLabel>
        <Input
          id="standard-adornment-search-player"
          type="text"
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                onClick={() => {
                  setPage(1);
                  setFetchPlayers(true);
                }}
                aria-label="toggle password visibility"
              >
                <SearchIcon />
              </IconButton>
            </InputAdornment>
          }
          onChange={(e: ChangeEvent<HTMLInputElement>) => {
            setFetchPlayers(false);
            setPlayerSearchItem(e.target.value);
          }}
        />
      </FormControl>

      <Grid container>
        <Grid item xs={1}></Grid>
        <Grid item xs={10} sx={{ display: "flex", justifyContent: "center" }}>
          <FormGroup
            sx={{
              mt: 3,
            }}
          >
            <FormControlLabel
              control={
                <Checkbox
                  checked={checked}
                  onClick={() => {
                    setFetchPlayers(false);
                    setChecked(!checked);
                  }}
                  sx={{
                    color: "#D76316",
                    "&.Mui-checked": {
                      color: "#D76316",
                    },
                  }}
                />
              }
              label="BOUGHT PLAYERS"
            />
          </FormGroup>
        </Grid>
        <Grid item xs={1}></Grid>
      </Grid>
    </>
  );
};

export default SearchPlayer;
