import * as React from "react";
import { styled, useTheme } from "@mui/material/styles";
import {
  Box,
  Drawer,
  CssBaseline,
  List,
  Divider,
  Button,
  IconButton,
  Pagination,
} from "@mui/material";

import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import { Player, useFilteredPlayers } from "../../../services/playersService";
import SearchPlayer from "../SearchPlayer/SearchPlayer";
import PlayerList from "../PlayerList/PlayerList";
import { useAtom } from "jotai";
import {
  currentPageAtom,
  fetchPlayersAtom,
  isBoughtAtom,
  isPlayersSidebarOpenAtom,
  playerSearchItemAtom,
  selectedWeekAtom,
} from "../../../utils/atoms";
import { ITEMS_PER_PAGE } from "../../../utils/constants";

const drawerWidth = 240;

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
  backgroundColor: "#02563C",
}));

export default function PlayersSidebar() {
  const theme = useTheme();

  const [playerSearchItem] = useAtom(playerSearchItemAtom);
  const [checked] = useAtom(isBoughtAtom);
  const [selected] = useAtom(selectedWeekAtom);
  const [fetchPlayers, setFetchPlayers] = useAtom(fetchPlayersAtom);
  const [page, setPage] = useAtom(currentPageAtom);

  const [open, setOpen] = useAtom(isPlayersSidebarOpenAtom);

  const { data: filteredPlayers, isLoading: isLoadingFiltered } =
    useFilteredPlayers(
      {
        playerSearchItem,
        isBought: checked,
        selected,
        page,
        itemsPerPage: ITEMS_PER_PAGE,
      },
      fetchPlayers
    );

  const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
    setFetchPlayers(true);
    setPage(value);
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Box>
      <CssBaseline />
      <Button
        aria-label="open drawer"
        onClick={handleDrawerOpen}
        variant="contained"
        sx={{
          ...(open && { display: "none" }),
          display: "flex",
          justifyContent: "center",
          margin: "auto",
          mt: 3,
          backgroundColor: "#02563C !important",
          color: "#FFF",
          borderRadius: "20px",
        }}
      >
        <FormatListBulletedIcon />
        VIEW ALL PLAYERS
      </Button>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
            backgroundColor: "#D8D6D9",
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <DrawerHeader>
          <h3
            style={{
              color: "#FFF",
              display: "flex",
              justifyContent: "start",
              margin: "auto",
            }}
          >
            PLAYERS
          </h3>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "ltr" ? (
              <ChevronLeftIcon fontSize="large" sx={{ color: "#FFF" }} />
            ) : (
              <ChevronRightIcon fontSize="large" sx={{ color: "#FFF" }} />
            )}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          <SearchPlayer />
          <PlayerList
            players={
              isLoadingFiltered
                ? ([] as Player[])
                : (filteredPlayers?.players as Player[])
            }
          />
          {!isLoadingFiltered && filteredPlayers?.players.length ? (
            <Pagination
              sx={{ mt: 1 }}
              color="secondary"
              count={Math.ceil(
                Number(filteredPlayers?.totalItems) / ITEMS_PER_PAGE
              )}
              page={page}
              onChange={handleChange}
            />
          ) : (
            ""
          )}
        </List>
      </Drawer>
    </Box>
  );
}
