import { useQuery, useMutation, useQueryClient } from "react-query";
import axios from "axios";

export type Pick = {
  _id: string;
  userId: string;
  bracketId: string;
  playerId: number;
};

export type CheckPickExists = {
  haveBeenMade: boolean;
  pick: Pick;
};

export type GetBracketPointsViewModel = {
  bracketPoints: number;
};

export type CreatePickInputModel = {
  bracketId: string;
  playerId: number;
};

export function useCheckIfPickHasBeenMade(bracketId: string) {
  return useQuery(["picks", bracketId], () =>
    checkIfPickHasBeenMade(bracketId)
  );
}

export function useGetWeekly(weekId: string) {
  return useQuery(["picks", weekId], () => getWeeklyBracketPoints(weekId));
}

export function useGetTotal() {
  return useQuery(["picks"], getTotalBracketPoints);
}

export function useCalculateWeekly() {
  const queryClient = useQueryClient();

  return useMutation(calculateWeeklyBracketPoints, {
    onMutate: () => {
      console.log("useCalculateWeekly: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("Points were updated!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["picks"]);
    },
  });
}

export function useCalculateTotal() {
  const queryClient = useQueryClient();

  return useMutation(calculateTotalBracketPoints, {
    onMutate: () => {
      console.log("useCalculateTotal: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("Total points were updated!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["picks"]);
    },
  });
}

export function useCreatePick() {
  const queryClient = useQueryClient();

  return useMutation(createPick, {
    onMutate: () => {
      console.log("useCreatePick: onMutate hook was triggered");
    },
    onSuccess: () => {
      console.log("New pick was added!");
    },
    onError: (error) => {
      console.error(error);
    },
    onSettled: () => {
      queryClient.invalidateQueries(["picks"]);
    },
  });
}

export const checkIfPickHasBeenMade = async (bracketId: string) => {
  const response = await axios.get<CheckPickExists>(
    `/api/v1/picks/haveBeenMade?bracketId=${bracketId}`
  );
  const result = response.data;

  return result;
};

export const calculateWeeklyBracketPoints = async (weekId: string) => {
  return await axios.patch("/api/v1/picks/calculateWeekly", { weekId });
};

export const calculateTotalBracketPoints = async () => {
  return await axios.patch("/api/v1/picks/calculateTotal");
};

export const getWeeklyBracketPoints = async (weekId: string) => {
  const response = await axios.get<GetBracketPointsViewModel>(
    `/api/v1/picks/calculateWeekly?weekId=${weekId}`
  );

  return response.data.bracketPoints;
};

export const getTotalBracketPoints = async () => {
  const response = await axios.get<GetBracketPointsViewModel>(
    `/api/v1/picks/calculateTotal`
  );
  return response.data.bracketPoints;
};

export const createPick = async (inputModel: CreatePickInputModel) => {
  return await axios.post("/api/v1/picks", inputModel);
};
