import React from "react";
import { Typography, Box, Divider } from "@mui/material";
import MatchPlayersCard from "../../components/containers/MatchDetails/MatchPlayersCard";
import MatchTournamentCard from "../../components/containers/MatchDetails/MatchTournamentCard";
import { useParams } from "react-router-dom";
import { useSingleMatch } from "../../services/matchesService";
import { Match } from "../../services/matchesService";
import MatchDetailTabs from "../../components/containers/MatchDetails/MatchDetailTabs";

const MatchDetails = () => {
  const { id } = useParams();
  const { data, isLoading, isError } = useSingleMatch(id);

  if (isLoading) {
    return <Typography>Loading...</Typography>;
  }

  if (isError) {
    return <Typography>Error Ocurred</Typography>;
  }

  if (!data?.match) {
    return <Typography>Not Found</Typography>;
  }

  const { tournamentId } = data?.match as Match;

  const { homeId, awayId, homeSets, awaySets, status, date } = data?.match;
  const matchPlayers = {
    homeId,
    homeSets,
    awayId,
    awaySets,
    status,
    date,
  };

  return (
    <>
      <Box sx={{ background: "#d3d3d3" }}>
        <MatchTournamentCard tournamentId={tournamentId} />
        <Divider sx={{ background: "white" }} />
        <MatchPlayersCard {...matchPlayers} />
      </Box>
      <MatchDetailTabs match={data?.match} />
    </>
  );
};

export default MatchDetails;
