import React, { FC, MouseEvent, useState } from "react";

import { useSinglePlayerMatches } from "../../../services/playersService";

import { Typography, Box, Grid, Chip, Stack, Divider } from "@mui/material";
import LastMatchCard from "./LastMatchCard";
import { HeadToHeadMatchViewModel } from "../../../services/matchesService";

type MatchHeadToHeadProps = {
  id: number;
  homeId: number;
  awayId: number;
};

const listComponent = (list?: HeadToHeadMatchViewModel[], playerId?: number) =>
  list?.map((m) => {
    return (
      <LastMatchCard
        key={m.id}
        date={m.date}
        homePlayer={m.homePlayer}
        awayPlayer={m.awayPlayer}
        homeSets={m.homeSets}
        awaySets={m.awaySets}
        winnerId={m.winnerId}
        playerId={playerId}
      />
    );
  });

const MatchHeadToHead: FC<MatchHeadToHeadProps> = ({ id, homeId, awayId }) => {
  const [surface, setSurface] = useState("All");

  const {
    data: homeObject,
    isLoading: homeMatchesIsLoading,
    isError: homeMatchesIsError,
  } = useSinglePlayerMatches(`${homeId}`);

  const {
    data: awayObject,
    isLoading: awayMatchesIsLoading,
    isError: awayMatchesIsError,
  } = useSinglePlayerMatches(`${awayId}`);

  if (homeMatchesIsLoading || awayMatchesIsLoading) {
    return <Typography>Loading...</Typography>;
  }

  if (homeMatchesIsError || awayMatchesIsError) {
    return <Typography>Error ocurred</Typography>;
  }

  const headToHeadMatches = homeObject?.matches.filter(
    (match) =>
      match.id !== id &&
      match.homePlayer.id === homeId &&
      match.awayPlayer.id === awayId
  );

  const filteredHomeMatches = homeObject?.matches.filter((match) =>
    surface === "All" ? true : match.tournament.surface === surface
  );

  const filteredAwayMatches = awayObject?.matches.filter((match) =>
    surface === "All" ? true : match.tournament.surface === surface
  );

  const handleClick = (event: MouseEvent<HTMLElement>) => {
    const surfaceValue =
      event.currentTarget.getElementsByTagName("span")[0].textContent;

    console.log(surfaceValue);

    setSurface(surfaceValue as string);
  };

  return (
    <Box sx={{ width: "100%" }} component="div">
      <Grid
        container
        gap={"5px"}
        sx={{
          minWidth: "280px",
          maxWidth: "420px",
          justifyContent: "center",
          margin: "0 auto",
        }}
      >
        <Grid item xs={6}>
          <Chip
            sx={{ width: "100%", minWidth: "70px" }}
            label="All"
            color="primary"
            onClick={handleClick}
          />
        </Grid>
        <Grid item xs={6}>
          <Chip
            sx={{ width: "100%" }}
            label="Outdoor Hard"
            color="primary"
            onClick={handleClick}
          />
        </Grid>
        <Grid item xs={6}>
          <Chip
            sx={{ width: "100%" }}
            label="Outdoor Clay"
            color="primary"
            onClick={handleClick}
          />
        </Grid>
        <Grid item xs={6}>
          <Chip
            sx={{ width: "100%" }}
            label="Outdoor Grass"
            color="primary"
            onClick={handleClick}
          />
        </Grid>
        <Grid item xs={6}>
          <Chip
            sx={{ width: "100%" }}
            label="Indoor Hard"
            color="primary"
            onClick={handleClick}
          />
        </Grid>
      </Grid>
      <Stack>
        <Typography variant="h5" sx={{ marginTop: "8px;", fontWeight: "900" }}>
          Last matches: {homeObject?.player.name}
        </Typography>
        {filteredHomeMatches?.length !== 0 ? (
          listComponent(filteredHomeMatches, homeObject?.player.id)
        ) : (
          <>
            <Divider flexItem />
            <Typography textAlign="center" padding={3}>
              No matches
            </Typography>
            <Divider flexItem />
          </>
        )}
        <Typography variant="h5" sx={{ marginTop: "8px", fontWeight: "900" }}>
          Last matches: {awayObject?.player.name}
        </Typography>
        {filteredAwayMatches?.length !== 0 ? (
          listComponent(filteredAwayMatches, awayObject?.player.id)
        ) : (
          <>
            <Divider />
            <Typography textAlign="center" padding={3}>
              No matches
            </Typography>
            <Divider />
          </>
        )}
        <Typography variant="h5" sx={{ marginTop: "8px", fontWeight: "900" }}>
          Last Head To Head Matches
        </Typography>
        {headToHeadMatches?.length !== 0 ? (
          listComponent(headToHeadMatches, undefined)
        ) : (
          <>
            <Divider />
            <Typography textAlign="center" padding={3}>
              No matches
            </Typography>
            <Divider />
          </>
        )}
      </Stack>
    </Box>
  );
};

export default MatchHeadToHead;
