import { FC } from "react";
import {
  Tournament,
  useSingleTournament,
} from "../../../services/tournamentsService";
import { Grid, Typography } from "@mui/material";
import ArrowForwardIcon from "@mui/icons-material/ArrowForwardIos";

type MatchTournamentCardProps = {
  tournamentId?: number;
};

const MatchTournamentCard: FC<MatchTournamentCardProps> = ({
  tournamentId,
}) => {
  const {
    data: tournament,
    isLoading: isTournamentLoaded,
    isError: isTournamentError,
  } = useSingleTournament(`${tournamentId}`);

  if (isTournamentLoaded) {
    return <Typography>Tournament Loading...</Typography>;
  }

  if (isTournamentError) {
    return <Typography fontWeight={"bolder"}>Error ocurred</Typography>;
  }

  const {
    countryName: tournamentCountryName,
    city: tournamentCity,
    code: tournamentCode,
    surface: tournamentSurface,
  } = tournament?.tournament as Tournament;

  return (
    <Grid container>
      <Grid
        item
        xs={12}
        sm={12}
        sx={{
          padding: "16px",
          borderRadius: "5px",
        }}
      >
        <Grid container alignItems={"center"}>
          <Grid item xs={3}>
            <img
              crossOrigin="anonymous"
              src={`https://countryflagsapi.com/png/${tournamentCountryName.toLowerCase()}`}
              style={{
                width: "90%",
                maxWidth: "250px",
                height: "auto",
                boxShadow:
                  "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
              }}
              alt=""
            />
          </Grid>
          <Grid item xs={6} justifySelf="center">
            <Typography style={{ display: "inline-block" }} variant="subtitle1">
              {tournamentCountryName}:
            </Typography>
            <Typography
              style={{
                display: "inline-block",
                fontWeight: "bolder",
                marginLeft: "3px",
              }}
              variant="subtitle1"
            >
              {tournamentCity} {tournamentCode}, {tournamentSurface} - {"R23"}
            </Typography>
          </Grid>
          <Grid item xs={3} textAlign="end">
            <ArrowForwardIcon fontSize={"medium"} />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12} sm={12}></Grid>
    </Grid>
  );
};

export default MatchTournamentCard;
