import { Grid, Typography } from "@mui/material";
import { useGetTotal as useTotalTeamPoints } from "../../../services/playersService";
import { useGetTotal as useTotalBracketPoints } from "../../../services/picksService";
import DashboardPoints from "../DashboardPoints/DashboardPoints";

const Dashboard = () => {
  const { data: teamPoints } = useTotalTeamPoints();
  const { data: bracketPoints } = useTotalBracketPoints();

  return (
    <>
      <Grid sx={{ mt: 2 }} container>
        <Grid item xs={6} display="flex" justifyContent="start">
          <Typography
            sx={{ fontSize: "15px", fontFamily: '"Kaushan Script", cursive' }}
          >
            <strong>DASHBOARD</strong>
          </Typography>
        </Grid>
        <Grid item xs={6} display="flex" justifyContent="end">
          <Typography
            sx={{ fontSize: "15px", fontFamily: '"Kaushan Script", cursive' }}
          >
            TOTAL: {Number(teamPoints) + Number(bracketPoints)}
          </Typography>
        </Grid>
      </Grid>

      <Grid sx={{ mt: 3 }} container>
        <Grid item xs={4}>
          <DashboardPoints
            points={Number(teamPoints)}
            type="TEAM"
            color="#D76316"
          />
        </Grid>
        <Grid item xs={4}>
          <DashboardPoints points={0} type="PREDICT" color="#507184" />
        </Grid>
        <Grid item xs={4}>
          <DashboardPoints
            points={Number(bracketPoints)}
            type="BRACKET"
            color="#02563C"
          />
        </Grid>
      </Grid>
    </>
  );
};

export default Dashboard;
