import React, { FC } from "react";
import { Card, CardContent, Grid, Typography } from "@mui/material";
import { Player } from "../../../services/tournamentsService";
import { useNavigate } from "react-router-dom";

type MatchCardProps = {
  id: number;
  homePlayer: Player;
  homePlayerSets: string;
  awayPlayer: Player;
  awayPlayerSets: string;
  winnerId: number;
};

const MatchCard: FC<MatchCardProps> = ({
  id,
  homePlayer,
  homePlayerSets,
  awayPlayer,
  awayPlayerSets,
  winnerId,
}) => {
  const navigate = useNavigate();
  const { id: homeId, name: homeName, country: homeCountry } = homePlayer;
  const { id: awayId, name: awayName, country: awayCountry } = awayPlayer;

  return (
    <Card
      sx={{ borderRadius: 0, cursor: "pointer" }}
      elevation={2}
      onClick={() => navigate(`/matches/${id}`)}
    >
      <CardContent>
        <Grid container alignItems="center">
          <Grid item xs={3}>
            <img
              crossOrigin="anonymous"
              src={`https://countryflagsapi.com/png/${homeCountry.toLowerCase()}`}
              style={{
                width: "90%",
                maxWidth: "150px",
                height: "auto",
                boxShadow:
                  "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
              }}
              alt=""
            />
          </Grid>
          <Grid item xs={8}>
            <Typography fontWeight={winnerId === homeId ? "bold" : ""}>
              {homeName}
            </Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography fontWeight={winnerId === homeId ? "bold" : ""}>
              {homePlayerSets}
            </Typography>
          </Grid>
        </Grid>
        <Grid container alignItems="center">
          <Grid item xs={3}>
            <img
              crossOrigin="anonymous"
              src={`https://countryflagsapi.com/png/${awayCountry.toLowerCase()}`}
              style={{
                width: "90%",
                maxWidth: "150px",
                height: "auto",
                boxShadow:
                  "rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px",
              }}
              alt=""
            />
          </Grid>
          <Grid item xs={8} fontWeight={winnerId === awayId ? "bold" : ""}>
            <Typography fontWeight={winnerId === awayId ? "bold" : ""}>
              {awayName}
            </Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography fontWeight={winnerId === awayId ? "bold" : ""}>
              {awayPlayerSets}
            </Typography>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

export default MatchCard;
