import { useQuery } from "react-query";
import axios from "axios";

export type UserWeek = {
  _id: string;
  userId: string;
  weekId: string;
  balls: number;
  balance: number;
  points: number;
  createdAt: string;
  updatedAt: string;
};

export type GetUserWeekViewModel = {
  userWeek: UserWeek;
};

// React Query Hooks

export function useUserWeek(weekId: string) {
  return useQuery(["team", weekId], () => getUserWeek(weekId));
}

// API methods

export const getUserWeek = async (weekId: string) => {
  const response = await axios.get<GetUserWeekViewModel>(
    `/api/v1/userWeeks/${weekId}`
  );
  const result = response.data;

  return result;
};
