import React from "react";

import { useAtom } from "jotai";
import { selectedWeekAtom } from "../../../utils/atoms";
import {
  PlayerInTeam,
  usePlayersInTeam,
} from "../../../services/playersService";
import { Grid } from "@mui/material";
import TeamPlayerCard from "../TeamPlayerCard/TeamPlayerCard";
import AddPlayerCard from "../AddPlayerCard/AddPlayerCard";

const countOfCards = [1, 2, 3, 4, 5, 6, 7, 8];

const TeamPlayerCardList = () => {
  const [selected] = useAtom(selectedWeekAtom);
  const { data, isLoading } = usePlayersInTeam({ selected });

  return (
    <>
      <Grid container>
        {!isLoading &&
          countOfCards.map((item) => {
            return Number(data?.players.length) >= item ? (
              <Grid
                sx={{ mt: 3 }}
                item
                xs={6}
                key={data?.players[item - 1].id}
                display="flex"
                justifyContent="center"
              >
                <TeamPlayerCard
                  player={data?.players[item - 1] as PlayerInTeam}
                  index={item}
                />
              </Grid>
            ) : (
              <Grid
                sx={{ mt: 3 }}
                item
                xs={6}
                key={item}
                display="flex"
                justifyContent="center"
                alignItems="start"
              >
                <AddPlayerCard />
              </Grid>
            );
          })}
      </Grid>
    </>
  );
};

export default TeamPlayerCardList;
