import { useAtom } from "jotai";
import { datePickerTournament } from "../../../atoms/atoms";
import { Box } from "@mui/material";
import { DatePicker, DatePickerProps } from "antd";
import dayjs from "dayjs";

const dateOptStyles = {
  backgroundColor: "#1976d2",
  padding: "15px",
  display: "flex",
  justifyContent: "center",
};

const TournamentFilter = () => {
  const [DatePickerTournament, setDatePickerTournament] =
    useAtom(datePickerTournament);

  const onChange: DatePickerProps["onChange"] = (date, dateString) => {
    setDatePickerTournament(date?.toDate() as Date);
  };

  return (
    <Box style={dateOptStyles}>
      <DatePicker
        style={{ width: "70vw", maxWidth: "720px", padding: "10px" }}
        onChange={onChange}
        value={dayjs(DatePickerTournament)}
      />
    </Box>
  );
};

export default TournamentFilter;
