import { useAtom } from "jotai";
import { datePickerTournament } from "../../../atoms/atoms";
import { Stack, Typography } from "@mui/material";
import TournamentCard from "./TournamentCard";
import { useTournamentsByDate } from "../../../services/tournamentsService";

const TournamentList = () => {
  const [date] = useAtom(datePickerTournament);
  const parsedDate = date?.toLocaleDateString("en-CA");
  const { data, isLoading, isError } = useTournamentsByDate(parsedDate);

  if (isError) {
    return <Typography color="danger">Error occurred</Typography>;
  }

  if (isLoading) {
    return <Typography>Loading...</Typography>;
  }

  return (
    <>
      <Stack gap={2}>
        {data?.tournaments.map((tournament) => {
          return (
            <TournamentCard
              key={tournament.id}
              id={tournament.id}
              tournamentName={tournament.name}
              countryName={tournament.countryName}
              tournamentCode={tournament.code}
              image={tournament.countryFlag}
              favorites={34}
              matches={tournament.matchesCount}
              date={parsedDate}
            />
          );
        })}
      </Stack>
    </>
  );
};

export default TournamentList;
