import { Typography } from "@mui/material";
import { useAtom } from "jotai";
import { FC } from "react";
import { Player } from "../../../services/playersService";
import { fetchPlayersAtom } from "../../../utils/atoms";
import PlayerCard from "../PlayerCard/PlayerCard";

export type PlayerListProps = {
  players: Player[];
};

const PlayerList: FC<PlayerListProps> = ({ players }) => {
  const [fetchPlayers] = useAtom(fetchPlayersAtom);

  return (
    <div style={{ marginTop: "20px" }}>
      {!fetchPlayers && (
        <Typography
          align="center"
          variant="button"
          display="block"
          gutterBottom
        >
          <strong>Click search icon for results...</strong>
        </Typography>
      )}
      {players?.map((player) => (
        <PlayerCard key={player.id} player={player} />
      ))}
    </div>
  );
};

export default PlayerList;
