import { FC } from "react";
import SportsBaseballIcon from "@mui/icons-material/SportsBaseball";
import {
  PlayerInTeam,
  useAddBallToPlayer,
  useDeleteBallFromPlayer,
} from "../../../services/playersService";
import { Paper, Typography, Grid, IconButton } from "@mui/material";
import { selectedWeekAtom } from "../../../utils/atoms";
import { useAtom } from "jotai";

const ballsPerPlayer = [1, 2, 3];

export type TeamPlayerBallsProps = {
  player: PlayerInTeam;
};

const TeamPlayerBalls: FC<TeamPlayerBallsProps> = ({ player }) => {
  const [selected] = useAtom(selectedWeekAtom);

  const { mutate: addBall } = useAddBallToPlayer();
  const { mutate: deleteBall } = useDeleteBallFromPlayer();

  const addBallToPlayer = () => {
    addBall({ weekId: selected.value, playerId: player.id });
  };

  const deleteBallFromPlayer = () => {
    deleteBall({ weekId: selected.value, playerId: player.id });
  };

  return (
    <>
      <Grid container>
        <Grid item xs={12} display="flex" justifyContent="center">
          <div>
            {ballsPerPlayer.map((item) => {
              return player.balls >= item ? (
                <IconButton key={item} onClick={deleteBallFromPlayer}>
                  <SportsBaseballIcon sx={{ color: "#DDC821", mt: 3 }} />
                </IconButton>
              ) : (
                <IconButton key={item} onClick={addBallToPlayer}>
                  <SportsBaseballIcon sx={{ color: "#FFE0B3", mt: 3 }} />
                </IconButton>
              );
            })}
          </div>
        </Grid>
        <Grid item xs={12} display="flex" justifyContent="center">
          <Paper
            sx={{
              mt: 2,
              px: 3,
              backgroundColor: "#02563C",
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Typography
              align="center"
              sx={{
                fontSize: "15px",
                color: "#FFF",
              }}
            >
              <strong>X{player.balls}</strong>
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export default TeamPlayerBalls;
