import { Button } from "@mui/material";
import { useCalculateTotal } from "../../../services/picksService";

const CalculateTotalBracket = () => {
  const { mutate: calculateTotal } = useCalculateTotal();

  const calculateTotalHandler = () => calculateTotal();

  return (
    <Button
      aria-label="open drawer"
      onClick={calculateTotalHandler}
      variant="contained"
      sx={{
        display: "flex",
        justifyContent: "center",
        margin: "auto",
        mt: 3,
        backgroundColor: "#02563C !important",
        color: "#FFF",
        borderRadius: "20px",
      }}
    >
      UPDATE TOTAL BRACKET POINTS
    </Button>
  );
};

export default CalculateTotalBracket;
