import { FC } from "react";

import { Paper, Typography } from "@mui/material";
import { selectedWeekAtom } from "../../../utils/atoms";
import { useAtom } from "jotai";
import { useUserWeek } from "../../../services/userWeeksService";

const WeeklyPoints = () => {
  const [selected] = useAtom(selectedWeekAtom);
  const { data } = useUserWeek(selected.value);

  return (
    <>
      <Paper
        sx={{
          backgroundColor: "#02563C",
          width: "100px",
          height: "100px",
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          margin: "auto",
        }}
      >
        <Typography
          sx={{
            color: "#FFF",
            fontSize: "22px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          variant="button"
          display="block"
        >
          <strong>{data?.userWeek.points}</strong>
        </Typography>
      </Paper>

      <Typography
        align="center"
        sx={{
          mt: 2,
          color: "#02563C",
          fontSize: "15px",
        }}
        variant="button"
        display="block"
      >
        <strong>{selected.text.toUpperCase()} POINTS</strong>
      </Typography>
    </>
  );
};

export default WeeklyPoints;
